@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;600;700;800;900&display=swap);
/*========== Block Settings ==========*/
* {
  box-sizing: border-box;
}
img {
  max-width: 100%;
  height: auto;
  width: 100%;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="tel"]:focus,
input[type="search"]:focus,
input[type="password"]:focus,
input[type="subject"]:focus,
input[type="phone"]:focus,
textarea:focus,
select:focus,
button:focus {
    outline: none;
    border: 2px solid #000;
}
input[type="text"],
input[type="email"],
input[type="url"],
input[type="tel"],
input[type="search"],
input[type="password"],
input[type="subject"],
input[type="phone"],
textarea {
    outline: 0;
    resize: none;
    font-weight: 400;
    display: block;
    width: 100%;
    line-height: 24px;
    height: 50px;
    font-size: 14px;
    border: 2px solid #d6e8f3;
    padding: 13px 20px;
    background: #fff;
    color: #02185a;
    border-radius: 0px;
    margin-bottom: 30px;
    box-shadow: none !important;
    font-family: 'Poppins', sans-serif;
}

textarea {
    height: 150px;
    padding-top: 20px;
}
textarea:focus {
    outline: none !important;
    box-shadow: none !important;
}
a,
a:hover,
a:active,
button:focus {
  text-decoration: none;
  border: none;
  outline: 0;
  color: #FFF;
}
ul
ol,
li{
  list-style: none;
  margin: 0;
  padding: 0;
  padding-left: 0;
}

::selection {
  background: #111;
  color: #fff;
}
/*========== Typography Settings ==========*/
h1,h2,h3,h4,h5,h6 {
	margin: 0;
    padding: 0;
    line-height: 1;
}
h1 {
    font-size: 55px;
    font-weight: 700;
}
h2 {

    font-size: 40px;
    font-weight: 500;
}
h3 {
    font-weight: 500;
    font-size: 23px;
}

h4 {

    font-size: 23px;
    font-weight: 500;
}

h5 {
    font-size: 20px;
    font-weight: 500;
}
p {
    line-height: 28px;
    font-weight: 400;
        font-family: 'Poppins', sans-serif;
    color: #666;
}


.btn-two {
  cursor: pointer;
  display: inline-block;
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  letter-spacing: 1px;
  font-weight: 600;
  text-transform: uppercase;
  padding: 6px 50px;
}


.btn-two {
  background: #000;
  color: #fff;
  border: 0;
  line-height: 48px;
}
.btn-two:hover {
  background: #FFF;
  color: #000;
  border: 0;
}

/*========== Spacing CSS ==========*/
.container {
  max-width: 1200px;
  margin-right: auto;
  margin-left: auto;
  padding: 0 15px;
  width: 100%;

}
body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #989595;
  line-height: 25px;
  background: #fff;
  box-sizing: border-box;
}
.main-start {
  max-width: 2000px;
  margin-right: auto;
  margin-left: auto;
  background: #000;
}

.display-table {
  width: 100%;
  height: 100%;
  display: table;
}
.table-cell {
  display: table-cell;
  vertical-align: middle;
}
/*----------------------------------------------------------------
        End Default Settings
----------------------------------------------------------------*/  

.row {

    display: flex;
    flex-wrap: wrap;
    margin-right: -10px;
    margin-left: -10px;
}
.item {
  flex: 0 0 33.333333%;
  max-width: 33.333333%;
  position: relative;
  width: 100%;
  min-height: 1px;
  padding-right: 10px;
  padding-left: 10px;
}

.section__section-title {
  padding-bottom: 30px;
}
.section__section-title h2 {
  font-size: 38px;
    color: #000;
    line-height: 38px;
    margin-bottom: 10px;
    font-weight: 700;
    text-transform: capitalize;
}

.section__section-title p {
  font-size: 14px;
  color: #666;
  line-height: 14px;
  font-weight: 600;
  margin: 0;
  margin-bottom: 15px;
  text-transform: uppercase;
}



.section_breadcrumb {
    background: #eaf1fa;
    height: 200px;
    min-height: 100%;
    width: 100%;
    position: relative;
    overflow: hidden;
    z-index: 222;
    text-align: left;
}
.title-bread {
  height: 200px;
}
.section_breadcrumb h1 {
    color: #000;
    display: block;
    margin: 0px;
    font-size: 50px;
    font-weight: 700;
    text-transform: capitalize;
}

.contact-us form {
  padding: 100px;
}

footer {
    background: #eef7fe;
    padding-top: 40px;
    padding-bottom: 40px;
    text-align: center;
    
}

footer .copyright {
    font-weight: 600;
    font-size: 14px;
    color: #000;
    letter-spacing: 1.7px;
    line-height: 28px;
    padding-top: 20px;
}
footer .copyright a {
    color: #000;
}

.social-media {
    padding-left: 0;
    padding-right: 0;
    margin-bottom: 0;
    margin-top: 0;
}
.social-media li {
    display: inline-block;
    margin-right: 5px;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: 5px;
}
.social-media li a {    
    font-size: 14px;
    width: 34px;
    height: 34px;
    line-height: 34px;
    display: inline-block;
    text-align: center;
    background: #000;
    color: #fff;
    transition: all .1s;
    
}
.social-media li a:hover {
    color: #000;
    background: #fff;
}

.section-portfolio {

}
.portfolio-box {
    margin-bottom: 30px;
    position: relative;

}
.portfolio-box p {
    margin: 10px 0 15px;
    font-weight: 600;
    line-height: 14px;
    font-size: 14px;
    text-transform: uppercase;
}

.portfolio-box h4 {
    font-size: 22px;
    font-weight: 700;
    color: #000;
    text-transform: capitalize;
    letter-spacing: 0.5px;
    line-height: 22px;
}
.section-profile {
    background: #fff;
    padding: 20px 0 50px;
}

.profile_skills {
    padding: 50px 0;
    overflow: hidden;
}

.profile_skills .profile {
    width: 50%;
    float: left;
}

.profile_skills .profile .profile-list {
    list-style: none;
    background-color: #ffffff;
    padding: 40px 40px 20px;
    position: relative;
    border: 3px solid #000;
    margin: 0;
    margin-right: 50px;
}

.profile_skills .profile .profile-list .profile-item {
    margin-bottom: 20px;
    color: #333;
    text-transform: capitalize;
    font-size: 14px;
    font-weight: 500;
    line-height: 14px;
}

.profile_skills .profile .profile-list .profile-item span{
    display: inline-block;
    width: 140px;
    text-transform: uppercase;
    font-weight: 600;

}

.profile_skills .profile .profile-list .profile-item span.web {
    font-weight: normal;
    color: #eb5424
}

.profile_skills .skills {
    width: 50%;
    float: left;
}

.skills .skill-box {
    margin-bottom: 50px;
}


.skills .skill-box:last-of-type {
    margin-bottom: 0;
}

.skills .skill-title {
    margin-bottom: 20px;
    color: #444;
    text-transform: uppercase;
    font-size: 18px;
    font-weight: 600;
}
.skills .skill-box .progress-line {
    background: #ddd;
    height: 5px;
    margin: 0;
    position: relative;
    width: 100%;
}

.skills .skill-box .progress-line > span {
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    height: 100%;
    width: 0;
    background: #000;
}

.skills .skill-box .progress-line > span > .percent-tooltip {
    position: absolute;
    right: -15px;
    top: -30px;
    margin-bottom: 10px;
    color: #444;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 600;
}

.section-work {
    padding: 100px 0 70px;
}

.work-item {
    margin-bottom: 30px;
    transition: 0.4s;
}

.content-box {
    padding: 40px 30px;
    background: #ffffff;

    border: 3px solid #000;
    text-align: center;
    transition: 0.4s;
}
.work-item:hover .content-box {
    box-shadow: 0px 3px 15px 3px rgb(0 0 0 / 6%);
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}
.work-item span {
    font-size: 35px;
    height: 70px;
    width: 70px;
    background: #000;
    border-radius: 6px;
    text-align: center;
    color: #fff;
    display: block;
    line-height: 70px;
    margin-right: auto;
    margin-left: auto;
     transition: 0.4s;
}
.work-item span i {
    font-weight: 100;
}
.work-item span::before {
    font-size: 35px;
    line-height: 70px;
}
.work-item:hover .content-box span {
     background: #000;
    color: #fff;
}
.work-item h4 {
    margin-top: 25px;
    font-size: 22px;
    font-weight: 700;
    color: #000;
    text-transform: capitalize;
    letter-spacing: 0.5px;
    line-height: 22px;
}

.work-item p {
    margin-bottom: 0;
    line-height: 26px;
    font-size: 14px;
    font-weight: 400;
    color: #000;
    font-family: 'Poppins', sans-serif;
}


.home {

   background-image: url(/static/media/banner.c8892369.jpg);
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    top: 0;
    left: 0;
    background-size: cover;
    height: 700px;
    min-height: 100%;
    width: 100%;
    position: relative;
    overflow: hidden;
    z-index: 222;
    text-align: left;
}

.home .overlay {
    background: linear-gradient(
        45deg
        , rgb(239 243 253 / 98%) 0%, rgb(238 254 255 / 98%) 100%);
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            min-height: 100%;
            height: 700px;

}
.info-header {
    height: 700px;
    min-height: 100%
}
.info-header .start {
    color: #000;
    font-size: 20px;
    font-weight: 400;
    margin-bottom: 10px;
    display: block;
    text-transform: capitalize;
}
.info-header h1 {
    color: #000;
    font-weight: 400;
    font-size: 50px;
    text-transform: capitalize;
    margin-bottom: 25px;
    
}
.info-header h1 span {
    display: block;
    margin-top: 20px;
