    text-transform: capitalize;
}
.banner {
    position: relative;
    z-index: 33;
}

.banner-meta {
    display: block;
    margin: 0 0 40px;
    padding-left: 0;
}
.banner-meta li {
    color: #000;
    display: inline;
    position: relative;
    letter-spacing: 3px;
    text-transform: uppercase;
    font-size: 1rem;
    font-weight: 700;
    line-height: 2;
}
.banner-meta li:first-of-type::before {
    top: -1px;
    content: " ";
    font-size: 11px;
    position: relative;
    color: currentcolor;
    padding:  0;
}
.banner-meta li::before {
    top: -1px;
    content: "|";
    font-size: 11px;
    position: relative;
    color: currentcolor;
    padding: 0 20px 0 17px;
}
.p-head {
    font-size: 16px;
    font-weight: 600;
    color: #fff;
    margin-bottom: 30px;
}
